<style lang="less" scoped>
    th, td {
        border: 1px solid #cad9ea !important;
        font-size: 14px !important;
        color: #666;
        height: 30px;
        padding: 5px 0px;
        vertical-align: middle;
        text-align: center;
    }
    th {
        background-color: #cce8eb;
        width: 100px;
    }

    tr:nth-child(odd) {
        background: #fff;
    }

    tr:nth-child(even) {
        background: #f5fafa;
    }

    table {
        border: 1px solid black;
        font-size: 14px !important;
        font-weight: 100;
        border-collapse: collapse;
        margin: 0 auto;
        text-align: center;
    }
    .title {
        text-align: center;
        font-size: 24px;
        padding: 15px;
        letter-spacing: 16px;
        line-height: 45px;
    }
    .score_box {
        bottom: -16%;
    }

    @media screen and (max-width: 768px) {
        .score {
            width: 100%;
            height: 100%;
            .score_box {
                height: 100%;
                width: 100%;
            }
        }
        table {
          margin: 20px;
        }
        .title {
            padding: 10px 20px;
        }
    } 
</style>

<template>
  <div class="score">
    <div class="score_box center">
       <div class="title">{{userinfo.class_name}}班级的{{userinfo.username}}同学成绩如下</div>
      <table>
        <thead>
          <th>序号</th>
          <th>专业</th>
          <th>课程名称</th>
          <th>单选题</th>
          <th>多选题</th>
          <th>判断题</th>
          <th>操作题</th>
          <th>成绩</th>
        </thead>
        <tbody>
          <tr v-for="(lesson,index) in date" :key="index">
            <td>{{ index }}</td>
            <td>{{ lesson.major_name }}</td>
            <td>{{ lesson.subject_name }}</td>
            <td>{{ lesson.single_scroe }}</td>
            <td>{{ lesson.selection_scroe }}</td>
            <td>{{ lesson.judge_scroe }}</td>
            <td>{{ lesson.not_read == 0 ? "未阅卷" : lesson.operation_scroe }}</td>
            <td>{{ lesson.not_read == 0 ? "阅卷未完成" : lesson.scroe }}</td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import {mapState} from 'vuex'
import axios from "../axios/index";
export default {
  async created() {
    let res = await axios.get("/api/getGrade",{
        params: {
            id_card: this.userinfo.id_card,
        }
    })
    this.date = res.scroe
  },
  computed: {
    // 拿到模块划分后的数据
       ...mapState({
           userinfo: state => state.user.userinfo
       }),
  },
  data() {
    return {
      date: []
    };
  }
};
</script>
