<style lang='less'>
   .m_input {
      input {
         width: 300px;
         height: 30px;
         font-size: 18px;
         border: none;
         border-bottom: 1px solid #fff;
         background: none;
         color: #fff;
         margin-bottom: 20px;
         outline: none;
         width: 300px;
         height: 30px;
         font-family: "neo";
         &::placeholder{
            color: rgba(255,255,255,0.8);
            font-size: 18px;
            font-family: "neo";
         }
         
      }
    }

    // 谷歌提示兼容
    input:-webkit-autofill,
    input:-webkit-autofill:hover,
    input:-webkit-autofill:focus,
    input:-webkit-autofill:active {
      font-size: 18px !important;
      font-family: "neo" !important;
      -webkit-transition-delay: 99999s;
      -webkit-transition: color 99999s ease-out, background-color 99999s ease-out;
    }

</style>

<template>
   <div class="m_input">
      <label>
         <span v-if="title != null">{{ title }}</span>
         <input @input="change" :type="type" :value="value" :placeholder="placeholder" />
      </label>
   </div>
</template>

<script>
export default {
   props: {
      title : {
         type: String,
         default: null
      },
      placeholder: {
         type: String,
         default: "用户身份证"
      },
      type: {
         type: String,
         default: "text"
      },
      value: {
         type: String,
         default: ""
      }
   },
   methods: {
       change(el) {
          // this.value = el.target.value
           this.$emit('input', el.target.value)
       }
   },
   data() {
      return {


      }
   },
}
</script>
