<style lang="less" scoped>
    .title {
        text-align: center;
        font-size: 36px;
        font-weight: bold;
        text-shadow: 0 3px 0px #404853, 0 6px 0px #333333;
        color: #97ABFF;
        padding-top: 255px;
        line-height: 45px;
        span {
            color: rgb(230, 204, 228);
        }
    }
    .other_box {
        text-align: center;
    }
</style>

<template>
    <div class="commit">
        <p class="title">恭喜提交成功<span>{{ second }}</span>秒后跳转到成绩查询页面</p>
        <div class="other_box">
            <m-button 
                :mstyle="{color: '#97ABFF', borderColor: '#97ABFF'}" 
                text="直接跳转"
                @callback="linkto"
            ></m-button>
        </div>
    </div>
</template>

<script>
import MButton from '../components/global/m-button'
export default {
   data() {
      return {
          second: 3
      }
   },
   methods: {
       linkto() {
           this.$router.push('/score')
       }
   },
   mounted() {
       this.timer = setInterval(() => {
           this.second --
           if (this.second <= 0) {
               this.$router.push('/score')
           }
       }, 1000)
   },
   destroyed() {
       clearInterval(this.timer)
       this.timer = null
   },
   components: {
       MButton
   }
}
</script>
