<style>
    .title {
        font-size: 14px;
        margin-bottom: 25px;
        /* max-height: 150px;
        overflow: scroll; */
    }
</style>

<template>
    <div class="title" v-html="title"></div>
</template>

<script>
export default {
    props: {
        count: {
            type: Number,
            default: 1
        },
        question: {
            type: String,
            default: "问题是?"
        }
    },
   computed: {  
       title() {
           return `第${this.count }题丶${this.question }`
       }
   }
}
</script>
