<style lang='less'>
    .process-wrapper {
        position: relative;
        height: 20px;
        background-color: white;
        border-radius: 16px; 
        .process-inner {
            transition: all 1s;
            position: absolute;
            top: 0;
            left: 0;
            bottom: 0;
            right: 0;
            border-radius: 16px; 
            background-color: #bec6dc;
            height: inherit;
            width: 20px;
        }
        .title {
            position: absolute;
            top: 0;
            left: 0;
            bottom: 0;
            right: 0;
            line-height: 20px;
            text-align: center;
            color: rgba(0, 0, 0, .6);
        }
    }
</style>

<template>
    <div class="process-wrapper">
        <div class="process-inner" :style="{width: rate}"></div>
        <div class="title">
            已完成{{ rate }}
        </div>
    </div>
</template>

<script>
export default {
    props: {
      rate: {
          type: String,
          default: "10%"
      }  
    },
   data() {
      return {


      }
   },
}
</script>
