<style lang="less">
    .tip {
         border: 1px solid rgba(255, 255, 255, .2);
         font-size: 14px;
         padding: 8px 4px;
         margin: 10px 0;
    }
</style>

<template>
    <div class="tip">
        <label>
            <input :checked='check' :data-type='dataType' @change="changeValue" :value="value" :name="name" :type="type">
            <span>{{ answer }}</span>
        </label>
    </div>
</template>

<script>
export default {
    props: {
        type: {
            type: String,
            default: "radio"
        },
        answer: {
            type: String,
            require: true
        },
        name: {
            type: String,
            required: true
        },
        value: {
            type: String,
            default: "0"
        },
        dataType: {
            type: String,
            default: null
        },
        check: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {


        }
    },
    methods: {
        changeValue(e) {
            this.$emit("changeValue", e)
        }
    }
}
</script>
