<style lang='less'>
    .login-wrpper-box {
       display: flex;
       box-shadow: 0px 0px 120px rgba(0, 0, 0, 0.45);
       border: none;
       border-radius: 8px;

        background: linear-gradient(45deg, #97ABFF, #123597);
        .pic-info { 
            background-color: transparent ;
            border-top-left-radius: 8px;
            border-bottom-left-radius: 8px; 
            width: 390px;
            .circle {
                width: 200px;
                height: 200px;
                background: url(../assets/image/shuai.jpg) center center;
                background-size: cover;
                background-color: #97ABFF;
                border-radius: 50%;
                margin: 25px auto;
                animation:mymove 8s infinite alternate;
            }
            .info {
                margin-top: 25px;
                p {
                    color: white;
                    text-align: center;
                    margin-bottom: 5px;
                }
            }
        }
    }
    

    @keyframes mymove
    {
        from {
            background-size:200px  200px;
        }
        to {
            background-size:800px  800px;
            background-position: -486px -267px;
        }
    }
    @media screen and (max-width: 768px) {
        .pic-info {
            display: none;
        }
        .login-wrpper-box {
            height: 100%;
            width: 100%;
            overflow: hidden;
        }
    } 
</style>

<template>
    <div class="login-page">
        
        <div class="center login-wrpper-box">
            <div class="pic-info">
                <div class="circle">

                </div>
                <div class="info">
                    <p>制作人: 刘友刚,张旭东,岳长春</p>
                    <p>制作时间: 2019</p>
                    <p>指导老师: 陈雪冬</p>
                </div>
            </div>
            <m-login-box></m-login-box>
        </div>
    </div>
</template>

