import Vue from "vue"
import Router from "vue-router"
import Login from "./views/login.vue"
import Answer from "./views/answer.vue"
import Commit from "./views/commit.vue"
import Score from "./views/score.vue"

import store from './store/index'

Vue.use(Router)

let router = new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/",
      name: "login",
      component: Login
    },
    {
      path: "/answer",
      name: "answer",
      component: Answer,
      meta: {
        auth: true
      }
    },
    {
      path: "/commit",
      name: "commit",
      component: Commit,
      meta: {
        auth: true
      }
    },
    {
      path: "/score",
      name: "score",
      component: Score,
      meta: {
        auth: true
      }
    }
  ]
})

router.beforeEach(async (to, from ,next)=>{
  await store.dispatch('user/initUserinfo')

  if (to.meta.auth) {
    // 若登陆直接跳转
    if (store.state.user.isLogin) {
      return next()
    }

    // 进行session请求判断
    return next({
      path: '',
      query: {
        redirect: to.path,
        errinfo: "请登陆账号后访问"
      }
    })
  }

  return next()
})

export default router
