<style lang='less'>
    .login_box {
       width: 390px;
       padding: 45px 2px;
       border-top-right-radius: 8px;
       border-bottom-right-radius: 8px; 
       background-color: transparent;
       text-align: center;
      .title {
         text-align: center;
         color: white;
         font-size: 24px;
         margin: 40px auto;
      }
      .btns {
         width: 300px;
         display: inline-block;
         margin-bottom: 40px;
         button:first-child {
            float: left;
         }
         button:last-child {
            float: right;
         }
      }
    }
   @media screen and (max-width: 768px) {
      .login_box {
         height: 100%;
         width: 100%;
      }
   }
</style>

<template>
    <div class="login_box">
      <h3 class="title neo">{{ title }}</h3>
      <div v-if="cur == 'login'">
         <m-input v-model="username"></m-input>
         <m-input v-model="password" type="password" placeholder="密码"></m-input>
         <div class="btns clearfix">
            <m-button text="登陆"  @callback='login'></m-button>
            <m-button text="前往修改密码"  @callback='gotoRepass'></m-button>
         </div>
      </div>
      <div v-if="cur == 'repass'">
         <m-input v-model="cusername"></m-input>
         <m-input v-model="cpassword" type="password" placeholder="原密码"></m-input>
         <m-input v-model="crepassword" type="password" placeholder="输入要修改的密码"></m-input>
         <div class="btns clearfix">
            <m-button text="修改密码" @callback="changePsd"></m-button>
            <m-button text="前往登陆页面" @callback='gotoLogin'></m-button>
         </div>
      </div>
      <!-- 数据返回提示 -->
      <!-- <el-button :plain="true" @click="open">打开消息提示</el-button> -->
    </div>
</template>

<script>
import {mapState, mapMutations} from "vuex"
import MInput from '../form/m-input'
import MButton from '../global/m-button'
import axios from '../../axios/index'
export default {
   mounted() {
      if (this.$route.query.errinfo) {
         this.showMessage(this.$route.query.errinfo, 2)
      }
   },
   data() {
      return {
         title: "考试系统登陆",
         // 场景切换判断
         cur: 'login',
         // 用户名密码
         username: '',
         password: '',
         cusername: '',
         cpassword: '',
         crepassword: '',
         isshow: false,
         message: "提示信息",
         isClick: false,
         // message提示信息
         boxMessage: null
      }
   },
   methods: {
      gotoRepass() {
         this.isClick = false
         this.cur = 'repass'
      },
      gotoLogin() {
         this.isClick = false
         this.cur = 'login'
         
      },
      // 重写消息框提示
      showMessage(message, type) {
         if (this.boxMessage) {
               this.boxMessage.close()
         }

         switch (type) {
               case 1:
                  this.boxMessage = this.$message(message)
                  break
               case 2:
                  this.boxMessage = this.$message.error(message)
                  break        
         }
      },
      async login() {
         // 防止多次点击
         if (this.isClick) return
         this.isClick = true

         // count: 请求的次数 limit: 每次请求的条数 
         let data = await axios.post("/api/login", {
            username: this.username,
            password: this.password
         })

         // console.log(data) 
         // console.log("登陆前用户信息:", this.userinfo)

         // 若数据反回失败报错
         if (!data) {
            this.isClick = false
            return
         }

         if (!data.code) {
            this.showMessage(data.msg || '登陆失败:账号或密码错误,请重试', 2)
         } else if(data.code == 2) {
            this.showMessage('网络请求超时', 2)
         } else if(data.code == 1) {
            this.showMessage(data.msg || '登陆成功', 1)
            // 请求成功存入vuex
            data.data.subject = data.major_subject
            this.init(data.data)

            // 路由跳转
            if(this.$route.query.redirect) {
               this.$router.push(this.$route.query.redirect)
            } else {
               this.$router.push("/answer")
            }
         }

         // 解锁
         this.isClick = false
         // console.log("登陆前后用户信息:", this.userinfo)
      },
      async changePsd() {
          // 防止多次点击
         if (this.isClick) return
         this.isClick = true

         let data = await axios.post("/api/changePassword", {
            username: this.cusername,
            cpassword: this.cpassword,
            crepassword: this.crepassword
         })

         // 若数据反回失败报错
         if (!data) {
            this.isClick = false
            return
         }
         
         if (!data.code) {
            this.showMessage(data.msg || '修改失败,请检查输入', 2)
         } else if(data.code == 2) {
            this.showMessage('网络请求超时', 2)
         } else if(data.code == 1) {
            this.showMessage(data.msg || '修改密码成功', 1)
            this.gotoLogin()
         }

         isClick = false
      },
      ...mapMutations('user', [
         'init',
      ])
   },
   computed: {
       ...mapState({
           userinfo: state => state.user.userinfo,
       })
   },
   components: {
      MInput,
      MButton,
   }
}
</script>
