import axios from "../axios/index"

export default {
  namespaced: true,
  state: {
    isLogin: false,
    userinfo: {
      msg: "未初始化数据",
    }
  },
  mutations: {
    init(state, userinfo){
      state.userinfo = userinfo
      state.isLogin = true
    },
    logout() {
      state.userinfo = {}
      state.isLogin = false
    }
  },
  actions: {
    async initUserinfo({commit}, userinfo) {
      let res = await axios.get("/api/getUserInfo")
      if (res.admin) {
        res.admin.subject = res.major_subject
        commit('init', res.admin)
      }
    }
  }
}
